var timer;
export default {
	data() {
		return {
			fsyzm:"发送验证码",
			fsFlag:true,
			nameFlag:false,
			yzmFlag:false,
			passFlag:false,
			rpassFlag:false,
			name:"",
			yzm:"",
			password:"",
			rpassword:""
		};
	},
	watch:{
		name(val){
			if(val == ""){
				this.nameFlag=false;
			}else{
				this.nameFlag=true;
			}
		},
		yzm(val){
			if(val == ""){
				this.yzmFlag=false;
			}else{
				this.yzmFlag=true;
			}
		},
		password(val){
			if(val == ""){
				this.passFlag=false;
			}else{
				this.passFlag=true;
			}
		},
		rpassword(val){
			if(val == ""){
				this.rpassFlag=false;
			}else{
				this.rpassFlag=true;
			}
		}
	},
	methods:{
		goTo(url){
			if(timer){
				clearInterval(timer);
			}
			uni.reLaunch({
				url:url
			})
		},
		clearFun(type){
			var that=this;
			switch (type){
				case 'name':
					that.name="";
					break;
				case 'yzm':
					that.yzm="";
					break;
				case 'password':
					that.password="";
					break;
				case 'rpassword':
					that.rpassword="";
					break;
				default:
					break;
			}
		},
		fsFun(){
			var that=this;
			if(that.fsFlag){
				that.fsFlag=false;
				var time=60;
				timer=setInterval(function(){
					console.log(time);
					time=time-1;
					that.fsyzm=time+"S";
					if(time < 0){
						that.fsFlag=true;
						clearInterval(timer);
						that.fsyzm="重新发送";
					}
				},1000)
			}
		}
	},
	onBackPress(){
		if(timer){
			clearInterval(timer);
		}
	}
}
